import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import axios from 'axios';
import DeleteIcon from '@material-ui/icons/Delete';
import { ListItemSecondaryAction, IconButton } from '@material-ui/core';
class ManageLinked extends React.Component {
  state ={
    accounts: [],
    loading: false
  }
  
  deleteAccount(number) {
    if(this.state.loading) return;
    this.setState({loading: true});
    axios.post('/web/delete-child', { email: this.state.accounts[number].email }, { headers: {'x-access-token': this.props.auth.user_token } })
      .then(response => {
        if(!response.data.error){
          this.updateList();
        }
      });
  }

  updateList() {
    axios.post('/web/get-child-list', { email: this.props.auth.user_email }, { headers: {'x-access-token': this.props.auth.user_token } })
      .then(response => {
        if(!response.data.error){
          if(response.data.data) {
            if (this.unmounted) return;
            this.setState({ accounts: response.data.data });
          }
        }
      });
  }

  componentDidMount() {
    this.updateList();
  }
  componentWillUnmount() {
    this.unmounted = true;
  }
  render() {
    return(
      <div>
        <List>

          {
            this.state.accounts.map( (data, number) => (
              <ListItem key={number} button>
                <ListItemText
                  primary={data.email}
                  secondary={`events: ${data.events.length}, bulletins: ${data.bulletins.length}, notifications: ${data.notifications.length}`}
                />
                <ListItemSecondaryAction>
                  <IconButton onClick={ () => this.deleteAccount(number) } aria-label="Delete">
                    <DeleteIcon />
                  </IconButton>
                </ListItemSecondaryAction>
              </ListItem>
            ))
          }
          
        </List>
      </div>
    );
  }
}
ManageLinked.propTypes = {
  auth: PropTypes.object.isRequired
};

const mapStateToProps = (state) => {
  return { 
    auth: state.auth
  };
};
export default connect(mapStateToProps) (ManageLinked);


