import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Button, Paper, Grid, CircularProgress, Snackbar, TextField, Typography, FormControlLabel, Checkbox, InputAdornment } from '@material-ui/core';

import classNames from 'classnames';

import green from '@material-ui/core/colors/green';

import axios from 'axios';
import Tree from './Tree';
const styles = (theme) => ({
  
  wrapper: {
    margin: theme.spacing.unit,
    position: 'relative',
    padding: 10
  },
  buttonSuccess: {
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[700],
    },
  },
  paperSend: {
    borderRadius: 6,
    padding: 10
  },
  buttonProgress: {
    color: green[500],
    position: 'absolute',
    top: '50%',
    left: '50%',
    marginTop: -12,
    marginLeft: -12,
  }
  
});

class CreateEmployability extends React.Component {
  state = {
    open: false,
    submitting: false,
    success: false,
    mssg: '',
    email: '',
    events: false,
    bulletins: false,
    notifications: false,
    password: ''
  };

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value
    });
  }
  handleChangeCheckbox = name => event => {
    this.setState({
      [name]: event.target.checked
    });
  }
  handleSubmit = () => {
    
    var formData = new FormData();
    if(this.state.email.length <= 3 || this.state.email.length >= 25 )
      return this.setState({open: true, mssg: 'password must be between 3 - 25 characters'});

    if(this.state.password.length <= 3 || this.state.password.length >= 25  )
      return this.setState({open: true, mssg: 'password must be between 3 - 25 characters'});

    let audienceRaw = sessionStorage.getItem('draft_audience');
    if(!audienceRaw) 
      return this.setState({open: true, mssg: 'please select an audience'});
    let audience = JSON.parse(audienceRaw).audience;
    let audienceData = [];
    for (const key of Object.keys(audience)) {
      audienceData.push(audience[key]._id);
    }
    if( audienceData.length === 0 ) {
      return this.setState({open: true, mssg: 'please select an audience'});
    }

    if( !this.state.events && !this.state.bulletins && !this.state.notifications  )
      return this.setState({open: true, mssg: 'please select atleast one role'});

    formData.append('email', this.props.auth.user_email.split('@')[0] + '-' + this.state.email + '@' + this.props.auth.user_email.split('@')[1]);
    formData.append('password', this.state.password);
    // formData.append('limits', audienceData);
    formData.append('audience', audienceData);
    formData.append('parent', this.props.auth.bundle.bundle.scope);
    formData.append('parent_email', this.props.auth.user_email);

    formData.append('events', this.state.events ? 'yes' : 'no' );
    formData.append('bulletins', this.state.bulletins ? 'yes' : 'no');
    formData.append('notifications', this.state.notifications ? 'yes' : 'no');

    this.setState( { submitting: true, success: false } );
    axios.post('/web/create-child', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
        'x-access-token': this.props.auth.user_token
      }
    }).then( response => {
      if(!response.data.error) {
        sessionStorage.setItem('draft_information', JSON.stringify({}));
        this.setState({ draft: { tags: [], name: '', description: '', list: [] }, submitting: false, success: false, open: true, mssg: 'success creting new child'});
      } else {
        this.setState({submitting: false, success: false, open: true, mssg: 'error creting new child'});
      }
    });
  }

  handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    this.setState({ open: false });
  };

  render() {
    const { classes } = this.props;
    const submitting = this.state.submitting;
    const loading = submitting;
    const success = this.state.success;
    const buttonClassname = classNames({
      [classes.buttonSuccess]: success,
    });
    return(
      <div className={classes.root}>
        <Grid container spacing={24}>
          <Grid item xs={12}>
            <Paper className={classes.paperSend}>
              <Typography variant="headline">Basic Details</Typography>
              
              <TextField
                label="email"
                fullWidth
                style={{marginBottom: 10}}
                InputProps={{
                  maxLength: 60,
                  startAdornment: <InputAdornment position="start">{this.props.auth.user_email.split('@')[0] + '-'}</InputAdornment>,
                  endAdornment: <InputAdornment position="start">{ '@' + this.props.auth.user_email.split('@')[1]}</InputAdornment>,
                }}
                onChange={this.handleChange('email')}
                value={this.state.email}
              />
              <TextField
                label="password"
                fullWidth
                style={{marginBottom: 10}}
                onChange={this.handleChange('password')}
                inputProps={{maxLength: 60, minLength: 4}}
                value={this.state.password}
              />

              <Typography variant="headline">Set Limits</Typography>
              <Tree/>

              <FormControlLabel
                control={
                  <Checkbox
                    checked={this.state.events}
                    onChange={this.handleChangeCheckbox('events')}
                    value="events"
                    color="primary"
                  />
                }
                label="Events"
              />
              <FormControlLabel
                control={
                  <Checkbox
                    checked={this.state.bulletins}
                    onChange={this.handleChangeCheckbox('bulletins')}
                    value="bulletins"
                    color="primary"
                  />
                }
                label="Bulletins"
              />
              <FormControlLabel
                control={
                  <Checkbox
                    checked={this.state.notifications}
                    onChange={this.handleChangeCheckbox('notifications')}
                    value="notifications"
                    color="primary"
                  />
                }
                label="Notifications"
              />

              <div className={classes.wrapper}>
                <Button
                  variant="raised"
                  color="primary"
                  fullWidth
                  className={buttonClassname}
                  disabled={loading}
                  onClick={this.handleSubmit}
                >
                      Create Account
                </Button>
                {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
              </div>

            </Paper>
            
          </Grid>
        </Grid>
        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={this.state.open}
          autoHideDuration={2000}
          onClose={this.handleClose}
          message={<span id="message-id">{this.state.mssg}</span>}                
        />
      </div>
    );
  }
}
CreateEmployability.propTypes = {
  classes: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired
};

const mapStateToProps = (state) => {
  return { 
    auth: state.auth
  };
};
export default connect(mapStateToProps) (withStyles(styles) (CreateEmployability));


